/*Animacio */
#div_loading{
	width: 100%;
	height: 100%;
	/*background-color:#FFFFFF;*/
	display: flex;
	overflow: hidden;
    position: absolute;
    top: 0;
    z-index:2001;
}

#loader2{
	left: 40%;
    line-height: 50%;
    position: relative;
    text-align: center;
    top: 40%;
}

* {
	box-sizing: border-box;
}

.loader {
  	position: relative;
   	left: 25%;
    line-height: 50%;
    position: relative;
    text-align: center;
    top: 40%;
  	width: 45%;
  	color: white;
  	font-family: "Roboto Condensed", sans-serif;
  	font-size: 250%;
  	background: linear-gradient(180deg, #222 0, #444 100%);
  	box-shadow: inset 0 5px 20px black;
  	text-shadow: 5px 5px 5px rgba(0,0,0,0.3);
}

.loader:after {
  	content: "";
  	display: table;
  	clear: both;
}

.loader span {
  	float: left;
  	height: 100px;
  	line-height: 120px;
  	width: 11%;
}

.loader > span {
  	border-left: 1px solid #444;
  	border-right: 1px solid #222;
}

.covers {
  	position: absolute;
  	height: 100%;
  	width: 100%;
}

.covers span div{
	height: 40px;
   	line-height: 40px;
    margin-left: 1px;
    margin-top: 39px;
    width: 40px;
    animation: up 3s infinite;
  	-webkit-animation: up 1s infinite;
}

#ani5 {
	animation: glow 3s infinite;
  	-webkit-animation: glow 1s infinite;
}

@keyframes glow{
  0%   { margin-bottom: 0; }
  	16%  {text-shadow:0 0 20px white;}
  	50% { margin-bottom: 0; }
  	100% { margin-bottom: 0;}
}

@-webkit-keyframes glow{
 	0%   { margin-bottom: 0; }
  	16%  {text-shadow:0 0 20px white; }
  	50% { margin-bottom: 0; }
  	100% { margin-bottom: 0;}
}

@keyframes up {
  	0%   { margin-bottom: 0;line-height: 40px; }
  	16%  { margin-top:1px; height: 50px; width: 50px;line-height: 50px; }
  	50% { margin-bottom: 0; line-height: 40px;margin-top:39px;}
  	100% { margin-bottom: 0;line-height: 40px; }
}

@-webkit-keyframes up{
 	0%   { margin-bottom: 0;line-height: 40px; }
  	16%  { margin-top:1px; height: 50px; width: 50px;line-height: 50px; }
  	50% { margin-bottom: 0; line-height: 40px;margin-top:39px;}
  	100% { margin-bottom: 0;line-height: 40px; }
}

#ani1 div  { animation-delay: .285714s; }
#ani2 div { animation-delay: .428571s; }
#ani3 div { animation-delay: .571428s; }
#ani4 div { animation-delay: .714285s; }
#ani5  { animation-delay: .714285s; }

